.projects {
  display: flex;
  flex-wrap: wrap;
  margin: -1.25em 0 0 -1.25em;
}

.project {
  background: white;
  display: flex;
  justify-content: center;
  justify-self: center;
  flex-direction: column;
  box-shadow: $shadow;
  margin: 1.25em 0 0 1.25em;
  width: calc(33.3333% - 1.25em);
  @media all and (max-width: 850px) {
    width: calc(50% - 1.25em);
  }
  @media all and (max-width: 550px) {
    width: 100%;
  }
}

.project__header {
  display: flex;
  justify-content: center;
  background: white;
  height: 40px;
  font-size: 15px;
  @media all and (max-width: 850px) {
    width: calc(50% - 1.25em);
  }
  @media all and (max-width: 550px) {
    width: 100%;
  }
}
.project__links {
  display: flex;
  padding-top: 10px;
  margin: auto;
}
